.vertical-scroll::-webkit-scrollbar {
  width: 1em;
  height: 1em;
}

.vertical-scroll::-webkit-scrollbar-track {
  background: white;
  border-radius: 100vw;
}

.vertical-scroll::-webkit-scrollbar-thumb {
  background: #0e516a;
  border: 0.25em solid white;
  border-radius: 100vw;
}

@supports (scrollbar-color: white  #0e516a) {
  * {
      scrollbar-color: #0e516a white;
  }
}
